'use client';

import Link from "next/link";
import { usePathname } from "next/navigation";
import Image from "next/image";
import { useRef, useState } from "react";
import { uploadAvatar } from "@/services/uploadAvatar"; // yukarıdaki fonksiyonu burada kullan
import useUserStore from "@/store/users";

export default function Sidebar() {
  const pathname = usePathname();
  const fileInputRef = useRef<HTMLInputElement | null>(null);
  const { user, setUser } = useUserStore();
  const [previewUrl, setPreviewUrl] = useState<string | null>(user?.avatarUrl || null);
  const [uploading, setUploading] = useState(false);

  const tabs = [
    { key: 'user', label: 'Profilim' },
    { key: 'referrals', label: 'Referanslar' },
    { key: 'apikeys', label: 'API Anahtarları' },
    { key: 'history', label: 'Giriş Geçmişi' },
    { key: '2fa', label: '2FA' },
    { key: 'password', label: 'Parola Değiştir' },
  ];

  const handleImageChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files || !user) return;

    const file = e.target.files[0];
    setUploading(true);

    try {
      const avatarUrl = await uploadAvatar(file, user.uid);
      setPreviewUrl(avatarUrl);

      const updatedUser = { ...user, avatarUrl };
      setUser(updatedUser);
      localStorage.setItem("user", JSON.stringify(updatedUser));
    } catch (err) {
      console.error("Avatar yükleme hatası:", err);
    } finally {
      setUploading(false);
    }
  };

  return (
    <div className="sidebar p-4 bg-white dark:bg-gray-900 rounded-xl shadow-md">
      <div className="flex flex-col items-center mb-6">
        <input
          type="file"
          accept="image/*"
          ref={fileInputRef}
          onChange={handleImageChange}
          className="hidden"
        />
        <div
          className="cursor-pointer relative"
          onClick={() => fileInputRef.current?.click()}
        >
          <Image
            src={previewUrl || "/images/avatar-placeholder.png"}
            alt="User Avatar"
            width={80}
            height={80}
            className="rounded-full border-2 border-gray-300 dark:border-gray-700"
          />
          {uploading && (
            <div className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-50 text-white text-xs rounded-full">
              Yükleniyor...
            </div>
          )}
        </div>
        <p className="mt-3 text-sm text-gray-700 dark:text-gray-300">
          {user?.email ?? "Misafir"}
        </p>
      </div>

      {tabs.map((tab) => (
        <Link key={tab.key} href={`/profile/${tab.key}`}>
          <div
            className={`tab px-4 py-2 rounded-lg mb-2 cursor-pointer transition-colors ${
              pathname.includes(tab.key)
                ? 'bg-blue-500 text-white'
                : 'text-gray-700 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-700'
            }`}
          >
            {tab.label}
          </div>
        </Link>
      ))}
    </div>
  );
}
